.social__list {
  display: flex;
  padding-left: 5px;
  align-items: center;
  gap: 15px;

  @include vp-767 {
    padding-left: 0;
  }
}

.social__item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__link {
  width: 40px;
  height: 40px;
  padding: 5px;
}

.social__icon {
  fill: $color-alice-blue-social;
  transition: $trans-default;
}

@media (hover: hover) {
  .social__icon:hover {
    fill: $color-deep-sky-blue;
  }
}
